.crawler__result {
}

.crawler__result-stats {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.crawler__result-stat {
  display: flex;
  justify-content: space-between;
  width: 50%;
  padding: 0 1rem;
  border-bottom: 1px solid #ccc;
  line-height: 2.5;
}

.crawler__result-stat:last-child {
  border: none;
}

.crawler__result-stat label {
  font-weight: bold;
  margin-bottom: 0;
}

.crawler__result-table-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.crawler__result-table {
  width: 100%;
  border-top: 1px solid #ccc;
  padding: 1rem;
}

.crawler__result-table table {
  width: 100%;
}

.crawler__result-table tr:nth-of-type(odd) {
  background: #eee;
}

.crawler__result-table th {
  background: #333;
  color: white;
  font-weight: bold;
}

.crawler__result-table td,
.crawler__result-table th {
  padding-top: 0.5rem;
  border: 1px solid #ccc;
  text-align: left;
  padding-left: 0.5rem;
}

@media only screen and (max-width: 760px) {
  .crawler__result-stat {
    display: flex;
    width: 100%;
    flex-direction: row;
    line-height: 1.5;
  }

  .crawler__result-stat-value {
    text-align: right;
  }

  .crawler__result-table table,
  .crawler__result-table thead,
  .crawler__result-table tbody,
  .crawler__result-table th,
  .crawler__result-table td,
  .crawler__result-table tr {
    display: block;
  }

  .crawler__result-table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  .crawler__result-table tr {
    border: 1px solid #ccc;
  }

  .crawler__result-table td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  .crawler__result-table td:before {
    position: absolute;

    top: 0.5rem;
    left: 0.5rem;
    width: 45%;
    padding-right: 0.5rem;
    white-space: nowrap;
    font-weight: bold;
    clear: both;
  }

  .crawler__result-table td:nth-of-type(1):before {
    content: "URL";
  }

  .crawler__result-table td:nth-of-type(2):before {
    content: "Code";
  }
}

@media only screen and (max-width: 760px) {
  .crawler__result-stat {
    padding: 0.25rem;
  }
  .crawler__result-table {
    padding: 0.25rem;
  }

  td {
    padding-left: 20%;
  }

  td:before {
    width: 75%;
  }

  td.crawler__result-table-url:before {
    display: none;
  }

  td.crawler__result-table-url {
    padding-left: 0.5rem;
  }
}
